import Layout from '../components/Layout'
import { useState } from 'react'
import { Small, Text } from '../components/styled'
const Contact = () => {

    const [state, setState] = React.useState({
        username: "",
        password: ""
    })

    const onChange = (e) => {
        const value = e.target.value;
        setState({
            ...state,
            [e.target.name]: value
        });
    }

    return <Layout>
        <div id="form-container">
            <form>
                <div className="form-group">
                    <label htmlFor="exampleInputEmail1">Email address</label>
                    <input value="username" type="username" placeholder="Enter username" />
                        <Small id="emailHelp" className="form-text text-muted">I don't judge you for being immature</Small>
                </div>
                <div className="form-group">
                    <label htmlFor="exampleInputPassword1">Password</label>
                    <input value="password" type="password" className="form-control" id="exampleInputPassword1" placeholder="Password" />
                </div>
                <button type="submit" className="btn btn-primary">Submit</button>
            </form>
        </div>
        <style jsx>{`
            .form-group {
                display: flex;
                flex-direction: column;
            }
            #form-container {
                width: 450px;
            }
        `}</style>
    </Layout>
}

export default Contact